.Input{
    width: 100%;
    outline: none;
    border: 2px solid rgb(138, 138, 138);
    border-radius: 5px;
    height: 25px;
    font-size: 17px;
}

.InputBlock h4{
    text-align: left;
    color: rgb(138, 138, 138);
    margin: 10px 0 0 0;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.invalid{
    border-bottom: 2px solid red;
}