.Header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgb(29, 37, 146);
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    /* border-bottom: 4px solid #bebebe; */
}

.HeaderLeft{
    display: flex;
    align-items: center;
}

.HeaderLeft img{
    width: 50px;
    width: 50px;
    margin: 0 0 0 10px;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.HeaderLeft ul{
    display: flex;
    list-style: none;
    z-index: 3;
}

.linksHolderLi a{
    padding: 0 15px;
}

.subLinkOff{
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.navBarItems{
    font-size: 16px;
    text-decoration: none;
    color: #fff;
    transition: all .2s;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.navBarItems:hover{
    opacity: .7;
}

.subitems{
    position: absolute;
    width: 100px;
    border: 2px solid #bebebe;
    border-radius: 5px;
    background-color: #fff;
}

.profileInfo{
    margin: 5px 0 0 0;
    font-size: 12px;
    color: #bebebe
}
