.DropDown{
    list-style: none;
    padding: 0 15px 0 0;
}

.DropDown p svg{
    width: 12px;
    fill: #fff;
    margin: 2px 0 0 5px;
}

.DropDown div{
    position: absolute;
    width: 200px;
    border-top: 1px solid #bebebe;
    border-radius: 0 0 5px 5px;
    background-color: #fff;
    color: #fff;
    overflow: hidden;
} 

.DropDown div a{
    border-top: 1px solid #bebebe;
    text-decoration: none;
    color: #fff;
    background: rgb(29, 29, 112);
    display: flex;
    align-items: center;
    transition: .3s;
    padding: 8px;
    font-size: 14px;
}

.DropDown div a:hover{
    opacity: .7;
}


