.MainPage{
    margin: 0 200px
}

.MainPage h2{
    color: rgb(138, 138, 138);
    margin-bottom: 0;
}

.links{
    border-top: 0px;
}

.linksBlock{
    width: 100%;
    margin: 20px 0 40px 0;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.linksBlock h3{
    color: #fff;
    margin: 0;
    border: 2px solid rgb(26, 33, 129);
    border-radius: 5px 5px 0 0;
    background-color: rgb(26, 33, 129);
    padding: 10px 20px;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.review{
    margin: 10px 20px;
    padding: 0;
}


