.Footer{
    background: rgb(29, 37, 146);
    color: #fff;
    font-size: 12px;
    border-radius: 20px 20px 0 0;
}

.FooterContainer{
    margin: 10px 30px;
}

.FooterContainer p{
    margin: 0;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;  
}

.FooterContainer a{
    text-decoration: none;
    color: #bebebe;
    transition: .2s;
}

.FooterContainer a:hover{
    color: #ccc
}

.FooterContent{
    display: flex;
    justify-content: space-between;
}

.FooterDrag{
    display: flex;
    justify-content: center;
    margin: -3px 0 0 0;
    transform: rotate(180deg);
}
