.TimeTableFilter_item{
    margin: 0;
    padding: 5px 11px;
    color: #fff;
    border: 1px solid rgb(29, 37, 146);
    background: rgb(29, 37, 146);
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
}

.TimeTableFilter_item{
    margin: 0 20px;
    padding: 5px 11px;
    color: #fff;
    border: 1px solid rgb(29, 37, 146);
    background: rgb(29, 37, 146);
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
    transition: .2s;
}

.TimeTableFilter_item:hover{
    opacity: .8;
} 

.activeB{
    opacity: .8;
}
