.TimeTableUI{
    margin: 20px 170px;
    background: rgb(138, 138, 138);
    border-top: 1px solid rgb(138, 138, 138);
    border-radius: 5px;
}

.tt-date{
    padding: 10px;
    color: #fff;
    border-radius: 5px;
}

.tt-pairs{
    background: rgb(88, 88, 88);
    border-bottom: 1px solid rgb(88, 88, 88);
    border-radius: 0 0 5px 5px;
}

.pairBlock{
    padding: 10px;
}

.pairInfo{
    display: flex;
    justify-content: space-between;
}

.pairInfo p{
    margin: 0;
}

.paitInfoItem{
    display: inline;
    font-size: 13px;
    padding: 2px 9px;
    color: #fff;
    margin: 0;
    background: rgb(29, 37, 146);
    border: 1px solid rgb(29, 37, 146);
    border-radius: 30px;
}

.pairInfoTime{
    padding: 0;
    font-size: 13px;
    color: rgb(194, 194, 194);
}

.pairSubject p{
    padding: 0 0 0 5px;
    margin: 0;
    color: #fff;
}

.teacherInfo{
    color: rgb(194, 194, 194)!important;
    font-size: 13px;
}