.Link{
    margin: 10px 20px;
}

.Link a{
    display: flex;
    align-items: center;
    text-decoration: none;
    margin: 0;
    padding: 0;
}

.Link img{
    width: 50px;
}

.Link p{
    color: rgb(26, 33, 129);
    font-size: 25px;
    margin: 0 0 0 15px;
    padding: 0;
    transition: .2s;
}

.Link p:hover{
    opacity: .8;
}