.Accordion{
    width: 100%;
    color: #fff;
    margin: 20px 0;
}

.acordion-icon{
    width: 20px;
    fill: #fff;
    transition: .2s
}

.Accordion p{
    margin: 0;
}

.top{
    z-index: 2;
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 10px 15px;
    background: rgb(26, 33, 129);
    border-radius: 10px;
    cursor: pointer;
    transition: .2s;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;  
}

.top:hover{
    background: rgb(29, 37, 146);
}

.date{
    font-size: 12px;
    color: rgb(212, 212, 212)
}

.head{
    font-size: 22px;
}

.text{
    z-index: 1;
    background: rgb(138, 138, 138);
    border-radius: 0 0 10px 10px;
}

.text div{
    padding: 10px 15px 10px 15px;
}

.top-opened{
    border-radius: 10px 10px 0 0;
}


.icon-closed{
    transform: rotate(180deg);
    transform-origin: 50% 50%;
}

