.Dialogs{
    margin: 0 200px;
}

.Dialogs h2{
    color: rgb(138, 138, 138);
    margin-bottom: 20px;
}

.Dialogs_block{
    display: flex;
    justify-content: space-between;
}

.d_item{
    border-left: 3px solid rgb(26, 33, 129);
    font-size: 22px;
    color: rgb(59, 59, 59);
    display: flex;
    align-items: center;
    margin: 0 0 20px 0;
    padding: 5px 10px;
    text-decoration: none;
    transition: .2s;
}
.d_item:hover{
    background: rgb(238, 238, 238);
}
.d_activeLink{
    border-left: 3px solid rgb(26, 33, 129);
    font-size: 22px;
    color: rgb(59, 59, 59);
    display: flex;
    align-items: center;
    margin: 0 0 20px 0;
    padding: 5px 10px;
    text-decoration: none;
    transition: .2s;
    background: rgb(238, 238, 238);
}

.d_activeLink img{
    width: 40px;
    padding: 0 10px 0 0;
}

.d_item img{
    width: 40px;
    padding: 0 10px 0 0;
}

.Dialogs_messages_block{
    width: 70%; 
    height: 390px;
}

.Dialogs_messages{
    position: relative;
    overflow: auto; 
    height: 365px;
    background: rgb(240, 240, 240);
    border-radius: 5px;
    scroll-snap-align: end;
}

.user_message{
    display: flex;
    justify-content: end;
    color: #fff;
    font-size: 18px;
    margin: 0 10px 0 0;
}

.user_message p{
    background: linear-gradient(to right, #0066cc 0%, #0099ff 100%);
    border-radius: 30px;
    padding: 5px 10px;
    margin: 10px 0;
}

.teacher_message{
    display: flex;
    justify-content: start;
    color: #fff;
    font-size: 18px;
    margin: 0 0 0 10px;
}

.teacher_message p{
    background: linear-gradient(to right, #33cccc 0%, #9999ff 100%);
    border-radius: 30px;
    padding: 5px 10px;
    margin: 10px 0;
}

.iconSend{
    border: 1px solid #0066cc;
    border-radius: 30px;
    background: #0066cc;
    fill: rgb(255, 255, 255);
    width: 20px;
    height: 20px;
    padding: 5px;
    margin: -2px 0 0 5px;
    cursor: pointer;
    transition: .2s;
}

.iconSend:hover{
    opacity: .85;
}

.Dialogs_input{
    display: flex;
    margin: 5px 0 0 0;
}

.Dialogs_input input{
    height: 25px;
    width: 100%;
    border: 1px solid rgb(211, 211, 211);
    background: rgb(240, 240, 240);
    border-radius: 5px;
    outline: none;
}