.dropHead{
    margin: 0;
    display: flex;
    cursor: pointer;
    font-size: 16px;
    text-decoration: none;
    color: #fff;
    transition: opacity .2s;
}

.dropHead:hover{
    opacity: .7;
}

.opened{
    opacity: .7;
}

.example-enter {
    opacity: 0.01;
  }
  
  .example-enter.example-enter-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
  
  .example-leave {
    opacity: 1;
  }
  
  .example-leave.example-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
  }