.ProfileDropDown{
    color: #fff;
    margin: 0 10px 0 0;
    padding: 0;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
}

.ProfileDropDown p svg{
    width: 21px;
    height: 21px;
    fill: #fff;
    margin: 2px 5px 0 0;
}

.ProfileDropDown div{
    z-index: 100;
    right: 10px;
    position: absolute;
    width: 400px;
    background: rgb(29, 29, 112);
    border-top: 2px solid #bebebe;
    border-radius: 0 0 5px 5px;
    color: #fff;
    overflow: hidden;
    padding: 0 10px;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none; 
} 

.ProfileDropDown div a{
    display: flex;
    align-items: center;    
    text-decoration: none;
    color: rgb(255, 255, 255, .9);
}

.ProfileDropDown div a svg{
    position: absolute;
    transform: rotate(-90deg);
    width: 25px;
    fill: rgb(255, 255, 255, .9);
    display: flex;
    right: 0;
    margin: 0 10px 0 0;
}

.ProfileDropDown div a ul{
    display: flex;
    flex-direction: column;
    width: 400px!important;
    list-style: none;
    background-color: rgb(48, 48, 129);
    border-radius: 5px;
    padding: 10px;
    margin: 10px 0;
    transition: .2s;
}

.ProfileDropDown div a ul:hover{
    background-color: rgb(48, 48, 129, .7);
}

.ProfileDropDown div a ul li{
    list-style: none;
    padding: 2px 0;
    font-size: 14px;
}

