.Button{
    display: inline-block;
    padding: 10px 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 13px;
    cursor: pointer;
}   

.LogOut{
    background-color: rgb(48, 48, 129);
    color: #fff;
    border: none;
    margin: 0 0 10px 0;
    transition: .2s;
    width: 100%;
}

.AuthButton{
    background-color: rgb(37, 37, 158);
    color: #fff;
    border: none;
    margin: 20px 0 0 0;
    padding: 10px 30px;
    transition: .2s;
}

.LogOut:hover{
    background-color: rgb(48, 48, 129, .7);
}

.AuthButton:hover{
    opacity: .9;
}

.Button:disabled{
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}